import React from "react"
import PersianFlagUp from "../../../../../src/index"
import "./text.scss"

const Text = () => {
  return (
    <div className="text example">
      <div className="after">
                
        <b>1234567890</b>
                      
        <b>""</b>
        
      </div>
      <div className="before">
        {PersianFlagUp.toPersianText(
          "                     1234567890       ''        "
        )}
      </div>
    </div>
  )
}

export default Text
